<template>
  <div>
    <button
      @click="toggleList"
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button custom-button--right custom-button--primary"
    >
      {{buttonName}}
    </button>
    <transition name="component-fade" mode="out-in">
      <data-table
        :columns="columns"
        :data="orders.data"
        :actionMethod="fetchRawMaterialPurchaseOrders"
        :count="orders.count"
        :searchPlaceholder="'Search by Name'"
        @rowSelected="orderSelected"
        v-if="showList"
      ></data-table>
      <raw-material-purchase-order-form :order="order" v-else @completed="toggleList"></raw-material-purchase-order-form>
    </transition>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { DataTable } from 'SHARED/data_table'
import { RawMaterialPurchaseOrderForm } from '.'
export default {
  name: 'raw-materials-orders-view',
  components: {
    DataTable,
    RawMaterialPurchaseOrderForm
  },
  computed: {
    ...mapGetters({
      orders: 'getRawMaterialPurchaseOrders'
    }),
    buttonName () {
      if (this.showList) {
        return 'Purchase'
      } else {
        return 'Back to List'
      }
    }
  },
  methods: {
    ...mapActions([
      'fetchRawMaterialPurchaseOrders'
    ]),
    toggleList () {
      this.showList = !this.showList
    },
    orderSelected (item) {
      this.$set(this, 'order', item)
      this.toggleList()
    }
  },
  data () {
    return {
      showList: true,
      columns: [
        {
          field: 'name',
          label: 'Vendor Name',
          type: String
        },
        {
          field: 'total_incl_tax',
          label: 'Total Amount',
          type: String
        },
        {
          field: 'pending_amount',
          label: 'Pending Amount',
          type: String
        },
        {
          field: 'status',
          label: 'Status',
          type: String
        }
      ],
      order: undefined
    }
  }
}
</script>
<style lang="scss" scoped>
.component-fade-enter-active, .component-fade-leave-active {
  transition: opacity .3s ease;
}
.component-fade-enter, .component-fade-leave-to
/* .component-fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



